    <!-- Paper abstract -->
    <section style="background-color: #f5f5f5; padding: 5em;">
      <el-row justify="space-around" style="margin-bottom: 0em;">
        <el-col :xs="23" :sm="21" :md="20" :lg="15" :xl="14">
          <div class="columns is-centered has-text-centered">
            <div class="column is-four-fifths">
              <h2 class="title is-3">Abstract</h2>
            </div>
          </div>
        </el-col>
      </el-row>
      <el-row justify="space-around">
        <el-col :xs="23" :sm="21" :md="20" :lg="15" :xl="14">
        <el-divider></el-divider>
          <div class="content has-text-justified">
            <p style=" font-size: 1.05em;">
              We propose a real-time path guiding method, Voxel Path Guiding (VXPG),
that significantly improves fitting efficiency under limited sampling budget.
Our key idea is to use a spatial irradiance voxel data structure across all
shading points to guide the location of path vertices. For each frame, we first
populate the voxel data structure with irradiance and geometry information.
To sample from the data structure for a shading point, we need to select
a voxel with high contribution to that point. To importance sample the
voxels while taking visibility into consideration, we adapt techniques from
offline many-lights rendering by clustering pairs of shading points and
voxels. Finally, we unbiasedly sample within the selected voxel while taking
the geometry inside into consideration.
            </p>
            <p>
              Our experiments show that VXPG
achieves significantly lower perceptual error compared to other real-time
path guiding and virtual point light methods under equal-time comparison.
Furthermore, our method does not rely on temporal information, but can
be used together with other temporal reuse sampling techniques such as
ReSTIR to further improve sampling efficiency.
            </p>
          </div>
        </el-col>
      </el-row>
    </section>

  <el-row justify="space-around" style="margin: 5em;">
    <el-col :xs="24" :sm="23" :md="20" :lg="15" :xl="14">
      <div class="columns is-centered has-text-centered">
        <div class="column is-four-fifths">
          <h2 class="title is-3">Key Insights</h2>
        </div>
      </div>
      <el-divider></el-divider>
      <!-- canvas zero -->
      <div style="text-align: center; width: 100%;">
        <canvas ref="canvas0" width="800" height="600" style="width: 100%;"></canvas>
        <el-button @click="draw_canvas_0" style="margin-top: 1em;">Roll the Dice Again</el-button>
      </div>
      <p style="margin-top: 1.0em; margin-bottom: 2.5em; font-size: 1.05em;">
        In path tracing, the direction of the next ray is typically determined by sampling the Bidirectional Scattering Distribution Function (BSDF) at the current shading point. However, this approach can become inefficient when only a small portion of the scene is illuminated. This inefficiency arises because BSDF sampling often fails to select samples that have non-zero incident radiance. In real-time rendering, where typically only one to two samples per pixel (SPP) are drawn, this can result in many pixels appearing black and a few exhibiting extremely high intensity, known as "fireflies." 
      </p>
      <!-- canvas first -->
      <div style="text-align: center; width: 100%;">
        <canvas ref="canvas1" width="800" height="600" style="width: 100%;"></canvas>
        <el-button @click="draw_canvas_1" style="margin-top: 1em;">Roll the Dice Again</el-button>
      </div>
      <p style="margin-top: 1.0em; margin-bottom: 2.5em; font-size: 1.05em;">
        To address this issue, <a href="https://cgg.mff.cuni.cz/~jirka/path-guiding-in-production/2019/index.htm" target="_blank">path guiding</a> involves learning a distribution that describes the incident radiance, thereby enabling sampling proportional to it. However, it's important to note that we still rely on BSDF sampling to figure out which directions have high incident radiance. With only one or two SPP, building the distribution itself remains a challenge. Therefore, previous work [<a href="https://cg.ivd.kit.edu/publications/2020/tsr_nee_pg_rtpt/paper.pdf" target="_blank">Dittebrandt et al. 2020</a>, <a href="https://diglib.eg.org/bitstream/handle/10.2312/egs20221024/025-028.pdf" target="_blank">Derevyannykh 2022</a>] often relies on temporal reuse to build the distribution, which can be quite problematic in highly dynamic scenes.
      </p>
      <!-- canvas first -->
      <div style="text-align: center; width: 100%;">
        <canvas ref="canvas2" width="800" height="600" style="width: 100%;"></canvas>
        <el-button @click="draw_canvas_2" style="margin-top: 1em;">Roll the Dice Again</el-button>
      </div>
      <p style="margin-top: 1.0em; margin-bottom: 2.5em; font-size: 1.05em;">
        Although it is very unlikely that every shading point can identify the high-contribution region, it is quite reasonable to expect that one or two lucky ones might find it, especially considering the large number of shading points (1280x720 for HD). Once a shading point locates the high-contribution region, it can highlight the voxel from which the radiance originates (shown as the green box). Therefore we ony use 1 SPP to collect information without temporal reuse.
      </p>
      <!-- canvas first -->
      <div style="text-align: center; width: 100%;">
        <canvas ref="canvas5" width="800" height="600" style="width: 100%;"></canvas>
        <div class="slider-demo-block" style="margin-left: 1%; margin-right: 1%;">
          <el-slider v-model="slider_canvas_1" :show-tooltip="false"/>
        </div>
        <p style="text-align: left; margin-top: 1.0em; margin-bottom: 2.5em; font-size: 1.05em;">
          Once the high-contribution voxels are identified, we attempt to connect the current shading points to these voxels, similar to the Next Event Estimation technique. This approach enables highly efficient global spatial sample reuse, which is pivotal to our method. For more detailed information on how to perform this "connection" in an efficient and unbiased manner, please refer to our paper :>.
        </p>
      </div>
    </el-col>
  </el-row>

