<script>
export default {
  name: 'VXPGView',
  components: {
  },
  data () {
    return {
      spotlight_icon: null,
      slider_canvas_1: 0,
      data_canvas_1: {
        movingPoint: 0,
      }
    }
  },
  watch: {
    slider_canvas_1(newVal) {
      this.data_canvas_1.movingPoint = newVal;
      this.draw_canvas_5();
    }
  },
  mounted() {
    this.loadAndConvertImage();
    this.draw_canvas();
    window.addEventListener('resize', this.onResize);
  },
  methods: {
    onResize () {
      this.draw_canvas();
    },
    loadAndConvertImage() {
      const img = new Image();
      // img.crossOrigin = 'Anonymous'; // Needed for external URLs
      img.onload = () => {
        this.spotlight_icon = img;
        this.draw_canvas();
      };
      img.src = 'https://imagehost-suikasibyl-us.oss-us-west-1.aliyuncs.com/img/flashlight-svgrepo-com.svg'; // Replace with your image URL
    },
    draw_canvas() {
      this.draw_canvas_0();
      this.draw_canvas_1();
      this.draw_canvas_2();
      this.draw_canvas_5();
    },
    draw_shading_point(ctx, x , y, point_radius) {
      ctx.fillStyle = '#F37F2E';
      ctx.beginPath();
      ctx.arc(x - point_radius/2, y, point_radius, 0, 2 * Math.PI);
      ctx.fill();
    },
    draw_line(ctx, p0, p1) {
      const point_radius = 6;
      ctx.beginPath();
      ctx.moveTo(p0.x - point_radius/2, p0.y);  // 移动到固定顶点
      ctx.lineTo(p1.x - point_radius/2, p1.y);  // 画线到可移动顶点
      ctx.stroke();
    },
    intersectRayWithSegment(rayOrigin, rayDirection, segmentStart, segmentEnd) {
      // Vector addition and scalar multiplication functions
      const add = (v1, v2) => ({ x: v1.x + v2.x, y: v1.y + v2.y });
      const multiply = (scalar, v) => ({ x: scalar * v.x, y: scalar * v.y });

      // Calculate vectors
      const v1 = { x: rayOrigin.x - segmentStart.x, y: rayOrigin.y - segmentStart.y };
      const v2 = { x: segmentEnd.x - segmentStart.x, y: segmentEnd.y - segmentStart.y };
      const v3 = { x: -rayDirection.y, y: rayDirection.x };

      // Solve for t1 and t2
      const dot = (v, w) => v.x * w.x + v.y * w.y;
      const t1 = (v2.x * v1.y - v2.y * v1.x) / dot(v2, v3);
      const t2 = (dot(v1, v3)) / dot(v2, v3);

      // Check if intersection is valid
      if (t1 >= 0 && t2 >= 0 && t2 <= 1) {
          const intersection = add(rayOrigin, multiply(t1, rayDirection));
          return intersection; // Intersection point
      }

      return null; // No valid intersection
    },
    shuffleArray(array) {
      for (let i = array.length - 1; i > 0; i--) {
          const j = Math.floor(Math.random() * (i + 1)); // Random index from 0 to i
          [array[i], array[j]] = [array[j], array[i]]; // Swap elements
      }
      return array;
    },
    draw_canvas_0() {
      const canvas = this.$refs.canvas0;
      const ctx = canvas.getContext('2d');

      const canvasWidth = canvas.offsetWidth;
      const canvasHeight = canvasWidth / 4;
      canvas.width = canvasWidth;
      canvas.height = canvasHeight;

      ctx.clearRect(0, 0, canvas.width, canvas.height);  // 清除画布

      // Draw the spotlight background
      ctx.beginPath();
      ctx.moveTo(canvasWidth * 2./3. + canvasWidth * 1./30., 0.3 * canvasHeight + 0.04 * canvasHeight);
      ctx.lineTo(canvasWidth * 2./3. + canvasWidth * 1./30., 0.3 * canvasHeight - 0.04 * canvasHeight);
      ctx.lineTo(0.99 * canvasWidth, 0.15 * canvasHeight);
      ctx.lineTo(0.99 * canvasWidth, 0.45 * canvasHeight);
      ctx.fillStyle = "#D2E5F9";
      ctx.fill();

      if(this.spotlight_icon){
        ctx.drawImage(this.spotlight_icon, 
        canvasWidth * 2./3., 
        0.3 * canvasHeight - canvasWidth * 0.7/15, 
        canvasWidth * 1./15., 
        canvasWidth * 1.4/15);
      }

      // Draw rectangle with all black lines
      ctx.beginPath();
      ctx.strokeStyle = 'black';
      ctx.lineWidth = 0.006*canvasWidth;
      ctx.rect(0.01 * canvasWidth, 
               0.03 * canvasHeight, 
               0.98 * canvasWidth, 
               0.94 * canvasHeight);
      ctx.stroke();

      // Draw the partially gray line on the right side of the rectangle
      ctx.beginPath();
      ctx.strokeStyle = '#409eff';
      ctx.moveTo(0.99 * canvasWidth, 0.15 * canvasHeight);
      ctx.lineTo(0.99 * canvasWidth, 0.45 * canvasHeight);
      ctx.stroke();
      
      let sp_count = 10;
      // create an array of size sp_count, set elements to 0
      let sp_array = Array(sp_count).fill(0).map((_, i) => i);
      for(let i=0;i<sp_count;++i) {
        sp_array[i] = (i + Math.random()) / sp_count;
      }
      sp_array = this.shuffleArray(sp_array);

      
      for(let i = 1; i < sp_count; i++) {
        const x = 0.01 * canvasWidth + ((i) / sp_count) * canvasWidth
        const y = 0.97 * canvasHeight;
        
        ctx.fillStyle = "#FFD07E";
        ctx.globalAlpha = 0.5;
        ctx.beginPath();
        ctx.ellipse(x, y, 
          0.035 * canvasWidth, 
          0.035 * canvasWidth, 
          0, Math.PI, 
          Math.PI * 2);
        ctx.fill();
        ctx.globalAlpha = 1.0;
        ctx.fillStyle = "#000";

        this.draw_shading_point(ctx, x, y, 0.006*canvasWidth);
        // random a uniform sample
        const random_d_sample = sp_array[i];
        // to a direction
        const random_d = {
          x: Math.cos(Math.PI * random_d_sample),
          y: -Math.sin(Math.PI * random_d_sample),
        };
        var next_point = this.intersectRayWithSegment({ x: x, y: y }, random_d, 
          { x: 0.99 * canvasWidth, y: 0.15 * canvasHeight }, 
          { x: 0.99 * canvasWidth, y: 0.45 * canvasHeight })
        if(next_point) {
          ctx.strokeStyle = '#7EE87F';
          ctx.setLineDash([0.005*canvasWidth, 0.005*canvasWidth]);
          this.draw_line(ctx, { x: x, y: y }, next_point);
          ctx.setLineDash([]);
          ctx.strokeStyle = '#5BB957';
        }
        else {
          ctx.setLineDash([]);
          ctx.strokeStyle = '#F16A65';
        }
        next_point = {
            x: x + random_d.x * 0.05 * canvasWidth,
            y: y + random_d.y * 0.05 * canvasWidth
          }
        this.draw_line(ctx, { x: x, y: y }, next_point);
      }
    },
    draw_canvas_1() {
      const canvas = this.$refs.canvas1;
      const ctx = canvas.getContext('2d');

      const canvasWidth = canvas.offsetWidth;
      const canvasHeight = canvasWidth / 4;
      canvas.width = canvasWidth;
      canvas.height = canvasHeight;

      ctx.clearRect(0, 0, canvas.width, canvas.height);  // 清除画布

      // Draw the spotlight background
      ctx.beginPath();
      ctx.moveTo(canvasWidth * 2./3. + canvasWidth * 1./30., 0.3 * canvasHeight + 0.04 * canvasHeight);
      ctx.lineTo(canvasWidth * 2./3. + canvasWidth * 1./30., 0.3 * canvasHeight - 0.04 * canvasHeight);
      ctx.lineTo(0.99 * canvasWidth, 0.15 * canvasHeight);
      ctx.lineTo(0.99 * canvasWidth, 0.45 * canvasHeight);
      ctx.fillStyle = "#D2E5F9";
      ctx.fill();

      if(this.spotlight_icon){
        ctx.drawImage(this.spotlight_icon, 
        canvasWidth * 2./3., 
        0.3 * canvasHeight - canvasWidth * 0.7/15, 
        canvasWidth * 1./15., 
        canvasWidth * 1.4/15);
      }

      // Draw rectangle with all black lines
      ctx.beginPath();
      ctx.strokeStyle = 'black';
      ctx.lineWidth = 0.006*canvasWidth;
      ctx.rect(0.01 * canvasWidth, 
               0.03 * canvasHeight, 
               0.98 * canvasWidth, 
               0.94 * canvasHeight);
      ctx.stroke();

      // Draw the partially gray line on the right side of the rectangle
      ctx.beginPath();
      ctx.strokeStyle = '#409eff';
      ctx.moveTo(0.99 * canvasWidth, 0.15 * canvasHeight);
      ctx.lineTo(0.99 * canvasWidth, 0.45 * canvasHeight);
      ctx.stroke();
      
      let sp_count = 10;
      // create an array of size sp_count, set elements to 0
      let sp_array = Array(sp_count).fill(0).map((_, i) => i);
      for(let i=0;i<sp_count;++i) {
        sp_array[i] = (i + Math.random()) / sp_count;
      }
      sp_array = this.shuffleArray(sp_array);
      
      for(let i = 1; i < sp_count; i++) {
        const x = 0.01 * canvasWidth + ((i) / sp_count) * canvasWidth
        const y = 0.97 * canvasHeight;

        // random a uniform sample
        const random_d_sample = sp_array[i];
        // to a direction
        var random_d = {
          x: Math.cos(Math.PI * random_d_sample),
          y: -Math.sin(Math.PI * random_d_sample),
        };
        
        if(Math.random() > 0.25) {
          const random_target = {
            x: 0.99 * canvasWidth, 
            y: 0.15 * canvasHeight + Math.random() * 0.3 * canvasHeight
          };
          random_d = {
            x: random_target.x - x,
            y: random_target.y - y
          };
          // normalize the direction
          const direction_length = Math.sqrt(random_d.x * random_d.x + random_d.y * random_d.y);
          random_d.x /= direction_length;
          random_d.y /= direction_length;
        }

        ctx.fillStyle = "#FFD07E";
        ctx.globalAlpha = 0.5;
        ctx.beginPath();
        const target = {
          x: 0.99 * canvasWidth,
          y: 0.3 * canvasHeight
        };
        const direction = {
          x: target.x - x,
          y: target.y - y
        };
        // normalize the direction
        const direction_length = Math.sqrt(direction.x * direction.x + direction.y * direction.y);
        direction.x /= direction_length;
        direction.y /= direction_length;
        // from direction to radius 
        const radius = Math.atan2(direction.y, direction.x);
        const offset = {
          x: ((0.05 - 0.02 * i / sp_count)) * canvasWidth * direction.x,
          y: ((0.05 - 0.02 * i / sp_count)) * canvasWidth * direction.y
        }
        ctx.ellipse(x + offset.x, y + offset.y, 
          ((0.05 - 0.02 * i / sp_count)) * canvasWidth, 
          (0.01 + 0.01 * i / sp_count) * canvasWidth, 
          radius, 0, 
          Math.PI * 2);
        ctx.fill();
        ctx.globalAlpha = 1.0;

        var next_point = this.intersectRayWithSegment({ x: x, y: y }, random_d, 
          { x: 0.99 * canvasWidth, y: 0.15 * canvasHeight }, 
          { x: 0.99 * canvasWidth, y: 0.45 * canvasHeight })
        if(next_point) {
          ctx.strokeStyle = '#7EE87F';
          ctx.setLineDash([0.005*canvasWidth, 0.005*canvasWidth]);
          this.draw_line(ctx, { x: x, y: y }, next_point);
          ctx.setLineDash([]);
          ctx.strokeStyle = '#5BB957';
        }
        else {
          ctx.setLineDash([]);
          ctx.strokeStyle = '#F16A65';
        }
        next_point = {
            x: x + random_d.x * 0.05 * canvasWidth,
            y: y + random_d.y * 0.05 * canvasWidth
          }
        this.draw_line(ctx, { x: x, y: y }, next_point);
        this.draw_shading_point(ctx, x, y, 0.006*canvasWidth);
      }
    },
    draw_canvas_2() {
      const canvas = this.$refs.canvas2;
      const ctx = canvas.getContext('2d');

      const canvasWidth = canvas.offsetWidth;
      const canvasHeight = canvasWidth / 4;
      canvas.width = canvasWidth;
      canvas.height = canvasHeight;

      ctx.clearRect(0, 0, canvas.width, canvas.height);  // 清除画布

      // Draw the spotlight background
      ctx.beginPath();
      ctx.moveTo(canvasWidth * 2./3. + canvasWidth * 1./30., 0.3 * canvasHeight + 0.04 * canvasHeight);
      ctx.lineTo(canvasWidth * 2./3. + canvasWidth * 1./30., 0.3 * canvasHeight - 0.04 * canvasHeight);
      ctx.lineTo(0.99 * canvasWidth, 0.15 * canvasHeight);
      ctx.lineTo(0.99 * canvasWidth, 0.45 * canvasHeight);
      ctx.fillStyle = "#D2E5F9";
      ctx.fill();

      if(this.spotlight_icon){
        ctx.drawImage(this.spotlight_icon, 
        canvasWidth * 2./3., 
        0.3 * canvasHeight - canvasWidth * 0.7/15, 
        canvasWidth * 1./15., 
        canvasWidth * 1.4/15);
      }

      // Draw rectangle with all black lines
      ctx.beginPath();
      ctx.strokeStyle = 'black';
      ctx.lineWidth = 0.006*canvasWidth;
      ctx.rect(0.01 * canvasWidth, 
               0.03 * canvasHeight, 
               0.98 * canvasWidth, 
               0.94 * canvasHeight);
      ctx.stroke();
      
      let sp_count = 50;
      let find = false;
      // create an array of size sp_count, set elements to 0
      let sp_array = Array(sp_count).fill(0).map((_, i) => i);
      for(let i=0;i<sp_count;++i) {
        sp_array[i] = (i + Math.random()) / sp_count;
      }
      sp_array = this.shuffleArray(sp_array);

      
      for(let i = 1; i < sp_count; i++) {
        const x = 0.01 * canvasWidth + ((i) / sp_count) * canvasWidth
        const y = 0.97 * canvasHeight;
        
        ctx.fillStyle = "#FFD07E";
        ctx.globalAlpha = 0.5;
        ctx.beginPath();
        ctx.ellipse(x, y, 
          0.025 * canvasWidth, 
          0.025 * canvasWidth, 
          0, Math.PI, 
          Math.PI * 2);
        ctx.fill();
        ctx.globalAlpha = 1.0;
        ctx.fillStyle = "#000";

        // random a uniform sample
        const random_d_sample = sp_array[i];
        // to a direction
        const random_d = {
          x: Math.cos(Math.PI * random_d_sample),
          y: -Math.sin(Math.PI * random_d_sample),
        };
        var next_point = this.intersectRayWithSegment({ x: x, y: y }, random_d, 
          { x: 0.99 * canvasWidth, y: 0.15 * canvasHeight }, 
          { x: 0.99 * canvasWidth, y: 0.45 * canvasHeight })
        if(next_point) {
          ctx.strokeStyle = '#7EE87F';
          ctx.setLineDash([0.005*canvasWidth, 0.005*canvasWidth]);
          this.draw_line(ctx, { x: x, y: y }, next_point);
          ctx.setLineDash([]);
          ctx.strokeStyle = '#5BB957';
          find = true;
        }
        else {
          ctx.setLineDash([]);
          ctx.strokeStyle = '#F16A65';
        }
        next_point = {
            x: x + random_d.x * 0.05 * canvasWidth,
            y: y + random_d.y * 0.05 * canvasWidth
          }
        this.draw_line(ctx, { x: x, y: y }, next_point);
      }
      
      for(let i = 1; i < sp_count; i++) {
        const x = 0.01 * canvasWidth + ((i) / sp_count) * canvasWidth
        const y = 0.97 * canvasHeight;
        this.draw_shading_point(ctx, x, y, 0.006*canvasWidth);
      }
      if (find) {
        ctx.globalAlpha = 0.8;
        ctx.beginPath();
        ctx.strokeStyle = '#7EE87F';
        ctx.moveTo(0.99 * canvasWidth, 0.12 * canvasHeight);
        ctx.lineTo(0.99 * canvasWidth, 0.48 * canvasHeight);
        ctx.lineWidth = 0.016*canvasWidth;
        ctx.stroke();
        ctx.globalAlpha = 1.0;
      }
      
      // Draw the partially gray line on the right side of the rectangle
      ctx.beginPath();
      ctx.lineWidth = 0.006*canvasWidth;
      ctx.strokeStyle = '#409eff';
      ctx.moveTo(0.99 * canvasWidth, 0.15 * canvasHeight);
      ctx.lineTo(0.99 * canvasWidth, 0.45 * canvasHeight);
      ctx.stroke();
    },
    draw_canvas_5() {
      const canvas = this.$refs.canvas5;
      const ctx = canvas.getContext('2d');

      const canvasWidth = canvas.offsetWidth;
      const canvasHeight = canvasWidth / 4;
      canvas.width = canvasWidth;
      canvas.height = canvasHeight;

      ctx.clearRect(0, 0, canvas.width, canvas.height);  // 清除画布

      // Draw the spotlight background
      ctx.beginPath();
      ctx.moveTo(canvasWidth * 2./3. + canvasWidth * 1./30., 0.3 * canvasHeight + 0.04 * canvasHeight);
      ctx.lineTo(canvasWidth * 2./3. + canvasWidth * 1./30., 0.3 * canvasHeight - 0.04 * canvasHeight);
      ctx.lineTo(0.99 * canvasWidth, 0.15 * canvasHeight);
      ctx.lineTo(0.99 * canvasWidth, 0.45 * canvasHeight);
      ctx.fillStyle = "#D2E5F9";
      ctx.fill();

      if(this.spotlight_icon){
        ctx.drawImage(this.spotlight_icon, 
        canvasWidth * 2./3., 
        0.3 * canvasHeight - canvasWidth * 0.7/15, 
        canvasWidth * 1./15., 
        canvasWidth * 1.4/15);
      }

      // Draw rectangle with all black lines
      ctx.beginPath();
      ctx.strokeStyle = 'black';
      ctx.lineWidth = 0.006*canvasWidth;
      ctx.rect(0.01 * canvasWidth, 
               0.03 * canvasHeight, 
               0.98 * canvasWidth, 
               0.94 * canvasHeight);
      ctx.stroke();

      // 
      const alpha_value = this.data_canvas_1.movingPoint / 100;
      var moving_point = {
        x: 0.,
        y: 0.
      };
      if(alpha_value < 4./9.) {
        const sub_alpha = alpha_value / (4./9.);
        moving_point.x = 0.95 * canvasWidth - sub_alpha * 0.90 * canvasWidth;
        moving_point.y = 0.96 * canvasHeight;
      }
      else if(alpha_value < 5./9.) {
        const sub_alpha = (alpha_value - (4./9.)) / (1./9.);
        moving_point.x = 0.01 * canvasWidth;
        moving_point.y = 0.99 * canvasHeight - sub_alpha * 0.94 * canvasHeight;
      }
      else {
        const sub_alpha = (alpha_value - (5./9.)) / (4./9.);
        moving_point.x = 0.01 * canvasWidth + sub_alpha * 0.94 * canvasWidth;
        moving_point.y = 0.03 * canvasHeight;
      }

      ctx.globalAlpha = 0.8;
      ctx.beginPath();
      ctx.strokeStyle = '#7EE87F';
      ctx.moveTo(0.99 * canvasWidth, 0.12 * canvasHeight);
      ctx.lineTo(0.99 * canvasWidth, 0.48 * canvasHeight);
      ctx.lineWidth = 0.016*canvasWidth;
      ctx.stroke();
      ctx.globalAlpha = 1.0;
      ctx.lineWidth = 0.006*canvasWidth;

      // 绘制顶点

      // random a uniform sample
      const random_v_sample = Math.random();
      const random_v = {
        x: 0.99 * canvasWidth,
        y: (0.15 + 0.3 * random_v_sample) * canvasHeight
      };

      ctx.strokeStyle = '#7EE87F';
      ctx.setLineDash([0.005*canvasWidth, 0.005*canvasWidth]);
      this.draw_line(ctx, moving_point, random_v);
      ctx.beginPath();
      ctx.setLineDash([]);

      this.draw_shading_point(ctx, moving_point.x, moving_point.y, 0.006*canvasWidth);

      // Draw the partially gray line on the right side of the rectangle
      ctx.beginPath();
      ctx.strokeStyle = '#409eff';
      ctx.moveTo(0.99 * canvasWidth, 0.15 * canvasHeight);
      ctx.lineTo(0.99 * canvasWidth, 0.45 * canvasHeight);
      ctx.stroke();
    }
  }
}
